
.navbar{
    background-color: #060b26;
    height: 60px;
    display: flex;
    justify-content: start;
    align-items: center;
    z-index: 7000!important;
}
.menu-bar{
    margin-left: 2rem;
    font-size: 2rem;
    background-color: none!important;
}
.nav-menu{
background-color: #060b26;
width: 250px;
height: 100vh;
display: flex;
justify-content: center;
position: fixed;
top: 0;
left: -100%;
transition: 850ms;
}
.nav-active{
    left: 0;
    transition: 350ms;
}
.nav-text{
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}
.nav-text a{
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 2px;
}
.nav-text a:hover{
    background-color: #1a83ff;
}
.nav-menu-items{
    padding: 0;
    width: 100%;
}
.navbar-toggle{
background-color: #060b26;
width: 100%;
height: 80px;
display: flex;
justify-content: start;
align-items: center;
}
span{
    position: relative;
    margin-left: 16px;
}